import React, { useState, useEffect, useRef, useCallback, memo } from "react";
import {
  View,
  Text,
  StyleSheet,
  TextInput,
  TouchableOpacity,
  ActivityIndicator,
  ImageBackground,
  Platform
} from "react-native";
import { useRoute, RouteProp, useNavigation } from "@react-navigation/native";
import * as SecureStore from "expo-secure-store";
import axios from "axios";
import { Ionicons } from "@expo/vector-icons";
import config from "../../../config/config_db";

let ToastAndroid: any = null;
if (Platform.OS === "android") {
  // S importa ToastAndroid no Android
  ToastAndroid = require("react-native").ToastAndroid;
}

type RootStackParamList = {
  PasswordAndSecurity: { email: string };
};

type PasswordSecurityScreenRouteProp = RouteProp<
  RootStackParamList,
  "PasswordAndSecurity"
>;

// Componente de input de senha, memorizado para evitar re-renderizaes desnecessrias.
interface PasswordInputProps {
  value: string;
  onChangeText: (text: string) => void;
  placeholder: string;
  secure: boolean;
  toggleSecure: () => void;
  userTheme: "dark" | "light";
}

const PasswordInput = memo(
  ({
    value,
    onChangeText,
    placeholder,
    secure,
    toggleSecure,
    userTheme,
  }: PasswordInputProps) => {
    return (
      <View
        style={[
          styles.inputContainer,
          { borderColor: userTheme === "dark" ? "#fff" : "#000" },
        ]}
      >
        <TextInput
          style={[
            styles.textInput,
            { color: userTheme === "dark" ? "#fff" : "#000" },
          ]}
          placeholder={placeholder}
          placeholderTextColor={userTheme === "dark" ? "#aaa" : "#555"}
          secureTextEntry={secure}
          value={value}
          onChangeText={onChangeText}
          blurOnSubmit={false}
        />
        <TouchableOpacity onPress={toggleSecure}>
          <Ionicons
            name={secure ? "eye-off" : "eye"}
            size={20}
            color={userTheme === "dark" ? "#fff" : "#000"}
          />
        </TouchableOpacity>
      </View>
    );
  }
);

PasswordInput.displayName = "PasswordInput";

// Componente para exibir o timer, isolado para que s ele seja atualizado a cada segundo.
interface TimerTextProps {
  timer: number;
  userTheme: "dark" | "light";
}

const TimerText = memo(({ timer, userTheme }: TimerTextProps) => (
  <Text
    style={[
      styles.info,
      userTheme === "light" ? { color: "#000" } : { color: "#fff" },
    ]}
  >
    Insira a nova palavra-passe. Para garantirmos a sua segurana, dentro de um 1 minuto a sesso ser encerrada (Ainda tm: {timer} segundos):
  </Text>
));

TimerText.displayName = "TimerText";

const PasswordSecurityScreen = () => {
  const route = useRoute<PasswordSecurityScreenRouteProp>();
  const { email } = route.params;
  const navigation = useNavigation();

  const url_verifyPassword = `${config.baseUrl}/calendarioFiles/passwordAndSecurity/verify_password.php`;
  const url_updatePassword = `${config.baseUrl}/calendarioFiles/passwordAndSecurity/update_password.php`;

  // Estados para configuraes do cliente
  const [backgroundUrl, setBackgroundUrl] = useState<string | null>(null);
  const [userTheme, setUserTheme] = useState<"dark" | "light">("light");
  const [settingsLoading, setSettingsLoading] = useState(true);

  // Estados do fluxo da tela
  const [step, setStep] = useState<"verify" | "update">("verify");
  const [currentPasswordInput, setCurrentPasswordInput] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [loading, setLoading] = useState(false);

  // Estados para mostrar/esconder senha
  const [currentPasswordSecure, setCurrentPasswordSecure] = useState(true);
  const [newPasswordSecure, setNewPasswordSecure] = useState(true);
  const [confirmPasswordSecure, setConfirmPasswordSecure] = useState(true);

  // Timer para a etapa "update" (em segundos)
  const [timer, setTimer] = useState(60);
  const timerRef = useRef<NodeJS.Timeout | null>(null);

  // Carregar configuraes do cliente usando SecureStore
  useEffect(() => {
    async function loadSettings() {
      try {
        const bg = await SecureStore.getItemAsync("backgroundUrl");
        setBackgroundUrl(bg);

        const mode = await SecureStore.getItemAsync("userTheme");
        setUserTheme(mode === "dark" ? "dark" : "light");
      } catch{
        console.log("Erro ao carregar configuraes.");
      } finally {
        setSettingsLoading(false);
      }
    }
    loadSettings();
  }, []);

  // Iniciar o timer quando a etapa for "update"
  useEffect(() => {
    if (step === "update") {
      setTimer(60);
      timerRef.current = setInterval(() => {
        setTimer((prev) => prev - 1);
      }, 1000);
    }
    return () => {
      if (timerRef.current) {
        clearInterval(timerRef.current);
        timerRef.current = null;
      }
    };
  }, [step]);

  // Monitorar o timer para avisos e expirao
  useEffect(() => {
    if (step === "update") {
      if (timer === 10) {
        if (Platform.OS === "web") {
          alert("A sesso expira em 10 segundos! Por favor, insira a nova palavra-passe ou cancele.");
        } else {
          ToastAndroid.show(
            "A sesso expira em 10 segundos! Por favor, insira a nova palavra-passe ou cancele.",
            ToastAndroid.LONG
          );
        }
      }
      if (timer <= 0) {
        if (Platform.OS === "web") {
          alert("Sesso expirada. Por favor, verifique a palavra-passe atual novamente.");
        } else {
          ToastAndroid.show(
            "Sesso expirada. Por favor, verifique a palavra-passe atual novamente.",
            ToastAndroid.LONG
          );
        }
        setStep("verify");
        setCurrentPasswordInput("");
        setNewPassword("");
        setConfirmPassword("");
      }
    }
  }, [timer, step]);

  // Funes para alternar a visibilidade da senha, memoizadas para no serem recriadas a cada renderizao
  const toggleCurrentPasswordSecure = useCallback(() => {
    setCurrentPasswordSecure((prev) => !prev);
  }, []);
  const toggleNewPasswordSecure = useCallback(() => {
    setNewPasswordSecure((prev) => !prev);
  }, []);
  const toggleConfirmPasswordSecure = useCallback(() => {
    setConfirmPasswordSecure((prev) => !prev);
  }, []);

  // Funo para verificar a palavra-passe atual
  const verifyPassword = async () => {
    if (!currentPasswordInput) {
      if (Platform.OS === "web") {
        alert("Por favor, insira a palavra-passe atual!");
      } else {
        ToastAndroid.show(
          "Por favor, insira a palavra-passe atual!",
          ToastAndroid.LONG
        );
      }
      return;
    }
    setLoading(true);
    try {
      const response = await axios.post(url_verifyPassword, {
        email,
        password: currentPasswordInput,
      });
      if (response.data.success) {
        setCurrentPasswordInput("");
        setStep("update");
      } else {
        if (Platform.OS === "web") {
          alert(response.data.message);
        } else {
          ToastAndroid.show(response.data.message, ToastAndroid.LONG);
        }
      }
    } catch (error) {
      if (Platform.OS === "web") {
        alert("Ocorreu um erro ao verificar a palavra-passe.");
      } else {
        ToastAndroid.show(
          "Ocorreu um erro ao verificar a palavra-passe.",
          ToastAndroid.LONG
        );
      }
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  // Funo para atualizar a palavra-passe
  const updatePassword = async () => {
    if (!newPassword || !confirmPassword) {
      if (Platform.OS === "web") {
        alert("Preencha todos os campos!");
      } else {
        ToastAndroid.show("Preencha todos os campos!", ToastAndroid.LONG);
      }
      return;
    }
    if (newPassword !== confirmPassword) {
      if (Platform.OS === "web") {
        alert("A nova palavra-passe e a confirmao no coincidem.");
      } else {
        ToastAndroid.show(
          "A nova palavra-passe e a confirmao no coincidem.",
          ToastAndroid.LONG
        );
      }
      return;
    }
    setLoading(true);
    try {
      const response = await axios.post(url_updatePassword, {
        email,
        newPassword,
      });
      if (response.data.success === true) {
        if (Platform.OS === "web") {
          alert("A sua palavra-passe foi atualizada com sucesso.");
        } else {
          ToastAndroid.show(
            "A sua palavra-passe foi atualizada com sucesso.",
            ToastAndroid.LONG
          );
        }
        setStep("verify");
        setCurrentPasswordInput("");
        setNewPassword("");
        setConfirmPassword("");
      } else {
        if (Platform.OS === "web") {
          alert(response.data.message);
        } else {
          ToastAndroid.show(response.data.message, ToastAndroid.LONG);
        }
      }
    } catch (error) {
      if (Platform.OS === "web") {
        alert("Ocorreu um erro ao atualizar a palavra-passe. Pedimos que tente novamente mais tarde.");
      } else {
        ToastAndroid.show(
          "Ocorreu um erro ao atualizar a palavra-passe. Pedimos que tente novamente mais tarde.",
          ToastAndroid.LONG
        );
      }
    } finally {
      setLoading(false);
    }
  };

  const cancelUpdate = () => {
    setStep("verify");
    setCurrentPasswordInput("");
    setNewPassword("");
    setConfirmPassword("");
    if (timerRef.current) {
      clearInterval(timerRef.current);
      timerRef.current = null;
    }
  };

  useEffect(() => {
    async function loadSettings() {
      try {
        let bg: string | null;
        let mode: string | null;
        // Se for web, usamos localStorage; se no, usamos SecureStore
        if (Platform.OS === "web") {
          bg = localStorage.getItem("backgroundUrl");
          mode = localStorage.getItem("userTheme");
        } else {
          bg = await SecureStore.getItemAsync("backgroundUrl");
          mode = await SecureStore.getItemAsync("userTheme");
        }
        // Se o background existir e no comear com "http", concatenamos com o baseUrl
        if (bg && !bg.startsWith("http")) {
          bg = `${config.baseUrl}/${bg}`;
        }
        setBackgroundUrl(bg);
        setUserTheme(mode === "dark" ? "dark" : "light");
      } catch (error) {
        console.log("Erro ao carregar configuraes.", error);
      } finally {
        setLoading(false);
      }
    }
    loadSettings();
  }, []);  

  const dynamicTextColor = userTheme === "dark" ? "#e0dede" : "#000";
  if (loading) { 
    return (
      <ImageBackground
        source={
          backgroundUrl
            ? { uri: backgroundUrl }
            : require("../../../assets/images/bg1.jpg")
        }
        style={{
          flex: 1,
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        {userTheme === "dark" && (
          <View
            style={{
              position: "absolute",
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              backgroundColor: "rgba(0,0,0,0.5)",
              zIndex: 1,
            }}
          />
        )}
        <View style={{ alignItems: "center" }}>
          <ActivityIndicator size="large" color={dynamicTextColor} />
          <Text style={{ fontSize: 18, marginTop: 10, color: dynamicTextColor }}>
            Carregando registros...
          </Text>
        </View>
      </ImageBackground>
    );
  }
  
  

  // Estilos condicionais conforme o tema
  const boxStyle = [
    styles.box,
    userTheme === "light"
      ? { backgroundColor: "#f0f0f0" }
      : { backgroundColor: "rgba(0, 0, 0, 0.5)" },
  ];
  const titleStyle = [
    styles.title,
    userTheme === "light" ? { color: "#000" } : { color: "#fff" },
  ];
  const buttonStyle = [
    styles.button,
    { backgroundColor: userTheme === "dark" ? "#333" : "#ccc" },
  ];
  const cancelButtonStyle = [
    styles.button,
    { backgroundColor: userTheme === "dark" ? "#630101" : "#f29d9d" },
  ];
  const backIconColor = userTheme === "light" ? "#000" : "#fff";

  return (
    <ImageBackground
    source={{ uri: backgroundUrl ? backgroundUrl : "../../../assets/images/bg1.png" }}
    style={{ flex: 1, justifyContent: "center", alignItems: "center" }}
  >
      {userTheme === "dark" && (
        <View style={styles.darkOverlay} pointerEvents="none" />
      )}

      <TouchableOpacity
        style={styles.backButton}
        onPress={() => navigation.goBack()}
      >
        <Ionicons name="arrow-back" size={24} color={backIconColor} />
      </TouchableOpacity>

      <View style={boxStyle}>
        <Text style={titleStyle}>Palavra-passe e Segurana</Text>
        {step === "verify" && (
          <>
            <Text
              style={[
                styles.info,
                userTheme === "light" ? { color: "#000" } : { color: "#fff" },
              ]}
            >
              Para garantir a sua segurana, insira a palavra-passe atual:
            </Text>
            <PasswordInput
              value={currentPasswordInput}
              onChangeText={setCurrentPasswordInput}
              placeholder="Palavra-passe atual"
              secure={currentPasswordSecure}
              toggleSecure={toggleCurrentPasswordSecure}
              userTheme={userTheme}
            />
            <TouchableOpacity style={buttonStyle} onPress={verifyPassword}>
              <Text
                style={[
                  styles.buttonText,
                  userTheme === "light" ? { color: "#000" } : { color: "#fff" },
                ]}
              >
                Verificar
              </Text>
            </TouchableOpacity>
          </>
        )}

        {step === "update" && (
          <>
            <TimerText timer={timer} userTheme={userTheme} />
            <PasswordInput
              value={newPassword}
              onChangeText={setNewPassword}
              placeholder="Nova palavra-passe"
              secure={newPasswordSecure}
              toggleSecure={toggleNewPasswordSecure}
              userTheme={userTheme}
            />
            <PasswordInput
              value={confirmPassword}
              onChangeText={setConfirmPassword}
              placeholder="Confirme a nova palavra-passe"
              secure={confirmPasswordSecure}
              toggleSecure={toggleConfirmPasswordSecure}
              userTheme={userTheme}
            />
            <TouchableOpacity style={buttonStyle} onPress={updatePassword}>
              <Text
                style={[
                  styles.buttonText,
                  userTheme === "light" ? { color: "#000" } : { color: "#fff" },
                ]}
              >
                Atualizar
              </Text>
            </TouchableOpacity>
            <TouchableOpacity style={cancelButtonStyle} onPress={cancelUpdate}>
              <Text
                style={[
                  styles.buttonText,
                  userTheme === "light" ? { color: "#000" } : { color: "#fff" },
                ]}
              >
                Cancelar
              </Text>
            </TouchableOpacity>
          </>
        )}
      </View>
    </ImageBackground>
  );
};

const styles = StyleSheet.create({
  darkOverlay: {
    ...StyleSheet.absoluteFillObject,
    backgroundColor: "rgba(0, 0, 0, 0.5)",
  },
  backButton: {
    position: "absolute",
    top: 50,
    left: 20,
    zIndex: 10,
    backgroundColor: "rgba(0, 0, 0, 0.2)",
    padding: 8,
    borderRadius: 20,
  },
  box: {
    padding: 20,
    borderRadius: 15,
    width: "90%",
    maxWidth: 400,
    alignItems: "center",
    justifyContent: "center",
  },
  title: {
    fontSize: 24,
    fontWeight: "bold",
    marginBottom: 16,
    textAlign: "center",
  },
  info: {
    fontSize: 16,
    marginBottom: 12,
    textAlign: "center",
  },
  inputContainer: {
    flexDirection: "row",
    alignItems: "center",
    width: "100%",
    height: 45,
    borderWidth: 1,
    borderRadius: 8,
    paddingHorizontal: 12,
    marginBottom: 16,
  },
  textInput: {
    flex: 1,
    fontSize: 16,
  },
  button: {
    paddingVertical: 12,
    paddingHorizontal: 20,
    borderRadius: 8,
    marginVertical: 10,
    alignItems: "center",
    justifyContent: "center",
    width: "100%",
  },
  buttonText: {
    fontSize: 16,
    fontWeight: "bold",
  },
  loadingWrapper: {
    flex: 1,
    justifyContent: "center",
    alignItems: "center",
  },
  // Caixa de loading menor
  loadingContainer: {
    padding: 20,
    borderRadius: 10,
    // Pode definir uma largura fixa se preferir, por exemplo: width: 150
    alignItems: "center",
    justifyContent: "center",
  },
  // Estilos para temas
  loadingDark: {
    backgroundColor: "rgba(0, 0, 0, 0.5)",
  },
  loadingLight: {
    backgroundColor: "rgba(255, 255, 255, 0.5)",
  }
});

export default PasswordSecurityScreen;