import React, { useState, useEffect } from "react";
import {
  View,
  StyleSheet,
  StatusBar,
  ActivityIndicator,
  KeyboardAvoidingView,
  Platform,
  TouchableOpacity,
  Image,
  ImageBackground,
  Alert,
  FlatList,
  Dimensions,
} from "react-native";
import {
  TextInput,
  Button,
  useTheme,
  Snackbar,
  Text,
  List,
  Divider,
} from "react-native-paper";
import {
  useRoute,
  RouteProp,
  useNavigation,
} from "@react-navigation/native";
import { StackNavigationProp } from "@react-navigation/stack";
import config from "../../../config/config_db";
import axios from "axios";
import * as SecureStore from "expo-secure-store";
import ModalConfig from "../../../components/modalConfig";

type RootStackParamList = {
  ProximosEventos: { email: string };
  Aluno: undefined;
};
type SugestAnythingScreenRouteProp = RouteProp<
  RootStackParamList,
  "ProximosEventos"
>;
type NavigationProp = StackNavigationProp<RootStackParamList>;

interface Suggestion {
  id: string;
  email: string;
  message: string;
  date_sent: string;
  response?: string;
  responder?: string;
  date_response?: string;
}

const SugestAnythingScreen: React.FC = () => {
  const theme = useTheme();
  const navigation = useNavigation<NavigationProp>();
  const route = useRoute<SugestAnythingScreenRouteProp>();
  const { email } = route.params;

  const [backgroundUrl, setBackgroundUrl] = useState<string | null>(null);
  const [userTheme, setUserTheme] = useState<"light" | "dark">("light");

  // estado do novo envio
  const [suggestion, setSuggestion] = useState<string>("");
  const [loadingSubmit, setLoadingSubmit] = useState<boolean>(false);
  const [snackbarMsg, setSnackbarMsg] = useState<string>("");
  const [snackbarVisible, setSnackbarVisible] = useState<boolean>(false);
  // lista de sugestes j enviadas
  const [suggestions, setSuggestions] = useState<Suggestion[]>([]);
  const [loadingList, setLoadingList] = useState<boolean>(true);

  const [accountModalVisible, setAccountModalVisible] = useState<boolean>(false);

  // carregar background e tema
  useEffect(() => {
    (async () => {
      try {
        let bg: string | null;
        let mode: string | null;
        if (Platform.OS === "web") {
          bg = localStorage.getItem("backgroundUrl");
          mode = localStorage.getItem("userTheme");
        } else {
          bg = await SecureStore.getItemAsync("backgroundUrl");
          mode = await SecureStore.getItemAsync("userTheme");
        }
        if (bg && !bg.startsWith("http")) bg = `${config.baseUrl}/${bg}`;
        setBackgroundUrl(bg);
        setUserTheme(mode === "dark" ? "dark" : "light");
      } catch {}
    })();
  }, []);

  // busca sugestes do utilizador
  const fetchSuggestions = async () => {
    setLoadingList(true);
    try {
      const res = await axios.get(
        `${config.baseUrl}/calendarioFiles/suggest/fetchUserSuggestion.php`,
        { params: { email } }
      );
      if (res.data.success) {
        setSuggestions(res.data.data);
      } else {
        Alert.alert("Erro", res.data.message || "Falha ao carregar sugestes");
      }
    } catch (err) {
      console.error(err);
      Alert.alert("Erro", "No foi possvel comunicar com o servidor.");
    } finally {
      setLoadingList(false);
    }
  };

  useEffect(() => {
    fetchSuggestions();
  }, []);

  const confirmBack = () => {
    if (Platform.OS === "web") {
      if (window.confirm("Tem a certeza que deseja sair?"))
        navigation.navigate("Aluno");
    } else {
      Alert.alert(
        "Confirmao",
        "Tem a certeza que deseja sair?",
        [
          { text: "Cancelar", style: "cancel" },
          { text: "Sim", onPress: () => navigation.navigate("Aluno") },
        ]
      );
    }
  };

  const handleSubmit = async () => {
    if (!suggestion.trim()) {
      setSnackbarMsg("Por favor, escreve alguma sugesto antes de enviar.");
      setSnackbarVisible(true);
      return;
    }
    setLoadingSubmit(true);
    try {
      const res = await axios.post(
        `${config.baseUrl}/calendarioFiles/suggest/submit_suggestion.php`,
        { email, suggestion }
      );
      if (res.data.success) {
        setSnackbarMsg("Sugesto enviada com sucesso!");
        setSuggestion("");
        await fetchSuggestions(); // refresca lista
      } else {
        setSnackbarMsg(res.data.message || "Falha ao enviar.");
      }
    } catch {
      setSnackbarMsg("Falha ao enviar. Tenta de novo mais tarde.");
    } finally {
      setLoadingSubmit(false);
      setSnackbarVisible(true);
    }
  };

  const renderSuggestion = ({ item }: { item: Suggestion }) => (
    <View>
      <List.Item
        title={item.message}
        description={() => (
          <>
            <Text>Enviado: {item.date_sent}</Text>
            {item.response ? (
              <Text>
                Resposta ({item.responder} em {item.date_response}):{" "}
                {item.response}
              </Text>
            ) : (
              <Text style={{ fontStyle: "italic" }}>Sem resposta ainda</Text>
            )}
          </>
        )}
        left={props => <List.Icon {...props} icon="message-text-outline" />}
      />
      <Divider />
    </View>
  );

  return (
    <ImageBackground
      source={
        backgroundUrl
          ? { uri: backgroundUrl }
          : require("../../../assets/images/bg1.jpg")
      }
      style={styles.background}
      resizeMode="cover"
    >
      <View style={styles.fullScreenContainer}>
        {/* HEADER */}
        <View
          style={[
            styles.header,
            {
              paddingTop:
                Platform.OS === "web"
                  ? 40
                  : StatusBar.currentHeight || 20,
            },
          ]}
        >
          <TouchableOpacity onPress={confirmBack} style={{ flex: 1 }}>
            <Image
              source={require("../../../assets/icons/angle-left.png")}
              style={[
                styles.icon,
                { tintColor: userTheme === "dark" ? "#FFF" : "#000" },
              ]}
            />
          </TouchableOpacity>
          <TouchableOpacity
            onPress={() => setAccountModalVisible(true)}
            style={{ flex: 1, alignItems: "flex-end" }}
          >
            <Image
              source={require("../../../assets/icons/user.png")}
              style={[
                styles.icon,
                { tintColor: userTheme === "dark" ? "#FFF" : "#000" },
              ]}
            />
          </TouchableOpacity>
        </View>

        {/* FORMULRIO */}
        <KeyboardAvoidingView
          style={styles.container}
          behavior={Platform.OS === "ios" ? "padding" : undefined}
        >
          <StatusBar
            barStyle={theme.dark ? "light-content" : "dark-content"}
            backgroundColor="transparent"
            translucent
          />

          <View style={styles.inner}>
            <Text style={styles.label}>
              Envia-nos uma sugesto de melhoria (email: {email})
            </Text>
            <TextInput
              mode="outlined"
              label="A tua sugesto"
              placeholder="Ex.: Melhorar o layout do calendrio..."
              value={suggestion}
              onChangeText={setSuggestion}
              multiline
              numberOfLines={4}
              style={styles.input}
            />
            {loadingSubmit ? (
              <ActivityIndicator
                animating
                size="large"
                style={styles.loader}
              />
            ) : (
              <Button
                mode="contained"
                onPress={handleSubmit}
                style={styles.button}
              >
                Enviar Sugesto
              </Button>
            )}
          </View>

          <Snackbar
            visible={snackbarVisible}
            onDismiss={() => setSnackbarVisible(false)}
            duration={3000}
          >
            {snackbarMsg}
          </Snackbar>

          {/* LISTA DE SUGESTES */}
          {loadingList ? (
            <ActivityIndicator style={{ marginTop: 20 }} />
          ) : (
            <FlatList
              data={suggestions}
              keyExtractor={i => i.id}
              renderItem={renderSuggestion}
              style={styles.list}
              contentContainerStyle={{ paddingBottom: 80 }}
            />
          )}
        </KeyboardAvoidingView>

        <ModalConfig
          visible={accountModalVisible}
          dynamicHeaderBackground="white"
          dynamicTextColor="black"
          onClose={() => setAccountModalVisible(false)}
          navigation={navigation}
          email={email}
        />
      </View>
    </ImageBackground>
  );
};

const styles = StyleSheet.create({
  background: { flex: 1 },
  fullScreenContainer: {
    flex: 1,
    backgroundColor: "transparent",
  },
  header: {
    width: "100%",
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
    paddingHorizontal: 10,
    paddingBottom: 10,
    position: "absolute",
    top: 0,
    zIndex: 10,
    backgroundColor: "white",
  },
  icon: {
    width: Platform.OS === "web" ? 35 : 23,
    height: Platform.OS === "web" ? 35 : 25,
    marginLeft: 3,
    marginTop: Platform.OS === "web" ? -15 : 3,
  },
  container: {
    flex: 1,
    paddingHorizontal: 16,
    paddingTop: 80,
  },
  inner: {
    marginBottom: 20,
  },
  label: {
    marginBottom: 8,
  },
  input: {
    minHeight: 100,
    marginBottom: 16,
  },
  button: {
    alignSelf: "flex-start",
  },
  loader: {
    marginVertical: 16,
  },
  list: {
    flex: 1,
  },
});

export default SugestAnythingScreen;